import { _decorator, Component, Node,instantiate, Button, UITransform, UI, Enum } from 'cc';
import { AS } from "db://as_framework/scripts//ASComponent";
import { UIPopup } from "db://as_framework/scripts/UIPopup";
import { ReplicatedButton } from "db://as_framework/scripts/ReplicatedButton";
import { toUSVString } from 'util';
const { ccclass, property, requireComponent } = _decorator;

enum AnswerType {
    SINGLE_CORRECT,
    MULTIPLE_CORRECT
}

@ccclass('MCQ')
export class MCQ extends AS(Component) {

    ///@property({type: Enum(AnswerType)}) private answerType = AnswerType.SINGLE_CORRECT;

   // @property({visible: function (this: MCQ) {return this.answerType === AnswerType.SINGLE_CORRECT}}) private x;
    private _selection =[] ;
    @property({type: Array}) private answerType;
    awake() {
        const insertButtonsComp = this.node.children;
        insertButtonsComp.forEach((child, i) => {
                child.addComponent(Button);
                child.addComponent(ReplicatedButton);
        })
        const choiceButtons = this.node.getComponentsInChildren(Button);
        choiceButtons.forEach((btn,i) => {
            this._selection.push(-1);
            console.log("running");
            btn.node.on(Button.EventType.CLICK,async()=>{
                
                if(this._selection[i]==-1) {
                    btn.node.children[0].active =true; 
                    this._selection[i]=i;
                }
                else{
                    btn.node.children[0].active=false;
                    this._selection[i]=-1;}
            })
        });    
    }
    checkSelection()
    {
       
    }
}

